import React from 'react'
import { Link as RLink } from "react-router-dom";

import CustomList from '../common/CustomList';

const TopBar = () => {
    const items = [
        {
            route: '/',
            name: 'Home',
            jsx:
                <RLink to='/'>
                    { 'Home' }
                </RLink>
        },
        {
            route: '/menu',
            name: 'Menu',
            jsx:
                <RLink to='/menu'>
                    { 'Menu' }
                </RLink>
        },
        {
            route: '/about',
            name: 'About',
            jsx:
                <RLink to='/about'>
                    { 'About' }
                </RLink>
        },
        {
            route: '/contact',
            name: 'Contact',
            jsx:
                <RLink to='/contact'>
                    { 'Contact' }
                </RLink>
        },
    ];

    return (
        <div id='topbar'>
            <div className='page-container'>
                <div>
                    <CustomList items={items} listOrientation={'h'} />
                </div>
            </div>
        </div>
    )
}

export default TopBar
