import React from 'react'
import { Link as RLink } from "react-router-dom";

const CustomList = ({ items, listOrientation }) => {
    return (
        <ul className={`menu ${listOrientation}`}>
            {
                items.map(el => 
                    <li>
                        { el.jsx }
                    </li>
                )
            }
        </ul>
    )
}

export default CustomList
