import React, { useState, useEffect } from 'react'
import { Link as RLink } from "react-router-dom";

import { BsFacebook, BsInstagram, BsShare } from 'react-icons/bs'

import CustomList from '../common/CustomList'
import CutsomModal from './CutsomModal';
import ShareWebsite from './ShareWebsite'

const SocialMediaPopup = () => {
    const [displayModal, setDisplayModal] = useState(false);

    const items = [
        {
            route: 'https://www.facebook.com/Apricus-GatroBar-106083671883634',
            name: 'Facebook',
            jsx: 
                <a href='https://www.facebook.com/Apricus-GatroBar-106083671883634' target='_blank'>
                    <BsFacebook />
                </a>
        },
        {
            route: '',
            name: 'Instagram',
            jsx: 
                <a href='https://instagram.com/apricus.gastrobar?utm_medium=copy_link' target='_blank'>
                    <BsInstagram />
                </a>
        },
        {
            route: '',
            name: 'Share',
            jsx:
                <a href="/" onClick={(e) => { 
                    e.preventDefault();
                    toggleShareModal(true);
                } }>
                    <BsShare />
                </a>
        }
    ];

    const toggleShareModal = (value) => {
        setDisplayModal(value);
    };

    return (
        <>
            <div id='social-media-popup-container'>
                <div className='open-popup'>
                    <p>
                        <BsShare />
                    </p>
                </div>

                <div className='popup-social-list'>
                    <CustomList items={items} listOrientation={'v'} />
                </div>
            </div>
            
            <CutsomModal content={<ShareWebsite />} display={displayModal} toggleShareModal={toggleShareModal} />
        </>
    )
}

export default SocialMediaPopup
