import React from 'react'
import { MdOutlineClose } from 'react-icons/md'

const CutsomModal = ({ content, display, toggleShareModal }) => {
    return (
        <div className={`modal-container ${display ? 'display-block' : 'display-none'}`}>
            <div className='close-modal'>
                <MdOutlineClose onClick={() => toggleShareModal(false)} />
            </div>
            {
                content
            }
        </div>
    )
}

export default CutsomModal
